body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.small-ui-margin {
  margin: 5px;
}

.content-center-flex {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content-center-row-flex {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
/* --- CSS Sprites --- */
.floor-tile {
  background: radial-gradient(circle at 30% 30%, rgba(255,255,255,0.7), rgba(255,255,255,0) 40%),
              linear-gradient(135deg, #cdd7e1, #b8c5d6);
  box-shadow: inset 0 1px 0 rgba(255,255,255,0.6);
  border-radius: 10px;
}
.wall-tile {
  background: linear-gradient(180deg, #6b7280, #4b5563);
  box-shadow: inset 0 4px 0 rgba(255,255,255,0.3), inset 0 -6px 12px rgba(0,0,0,0.5);
  border-radius: 12px;
}
.goal-marker {
  background: radial-gradient(circle at center, rgba(255, 215, 0, 0.85), rgba(255, 165, 0, 0.9) 60%, rgba(255, 140, 0, 0.7));
  filter: drop-shadow(0 0 6px rgba(255, 180, 0, 0.6));
  border-radius: 50%;
}
.box-sprite {
  background: linear-gradient(180deg, #c48a3b, #a66b1f);
  border-radius: 10px;
  box-shadow: inset 0 3px 0 rgba(255,255,255,0.45), inset 0 -6px 8px rgba(0,0,0,0.35), 0 3px 6px rgba(0,0,0,0.25);
  position: relative;
}
.box-sprite::before, .box-sprite::after {
  content: "";
  position: absolute;
  inset: 10% 14%;
  border: 2px solid rgba(0,0,0,0.25);
  border-radius: 6px;
}
.player-sprite {
  background: radial-gradient(circle at 30% 25%, #ffffff, #9dd4ff 40%, #3b82f6 70%, #1d4ed8);
  border-radius: 50%;
  box-shadow: inset 0 3px 8px rgba(255,255,255,0.7), 0 6px 10px rgba(0,0,0,0.25);
  position: relative;
}
.player-sprite::before, .player-sprite::after {
  content: "";
  position: absolute;
  top: 32%; width: 18%; height: 18%; border-radius: 50%;
  background: #0f172a;
  box-shadow: inset 0 -1px 0 rgba(255,255,255,0.3);
}
.player-sprite::before { left: 26%; }
.player-sprite::after { right: 26%; }
